//app/dashboard/products/edit/[id]/page.js
'use client';
import { useRouter } from 'next/navigation';
import { fetchProductById, updateProduct } from '@/lib/api';
import { useEffect, useState } from 'react';

export default function EditProductPage({ params }) {
  const router = useRouter();
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Extract id from params
  const { id } = params;

  useEffect(() => {
    const loadProduct = async () => {
      try {
        if (!id) {
          throw new Error('Product ID is missing');
        }
        
        const data = await fetchProductById(id);
        setProduct(data);
      } catch (err) {
        console.error('Error loading product:', err);
        setError(err.message || 'Failed to load product');
      } finally {
        setLoading(false);
      }
    };
    
    loadProduct();
  }, [id]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formData = Object.fromEntries(new FormData(e.target));
    
    try {
      if (!id) {
        throw new Error('Product ID is missing');
      }
      
      await updateProduct(id, {
        ...formData,
        categories: formData.categories.split(',').map(c => c.trim()),
        price: Number(formData.price),
        stock: Number(formData.stock)
      });
      router.push('/dashboard/products');
    } catch (err) {
      console.error('Error updating product:', err);
      alert('خطا در ویرایش محصول: ' + err.message);
    }
  };

  if (loading) return <div className="p-4">در حال بارگذاری...</div>;
  if (error) return <div className="p-4 text-red-600">{error}</div>;
  if (!product) return <div className="p-4 text-red-600">محصول یافت نشد</div>;

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-6">ویرایش محصول</h1>
      
      <form onSubmit={handleSubmit} className="bg-white p-6 rounded-lg shadow">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">نام محصول</label>
            <input
              type="text"
              name="name"
              defaultValue={product.name}
              required
              className="w-full p-2 border border-gray-300 rounded"
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">قیمت (ریال)</label>
            <input
              type="number"
              name="price"
              defaultValue={product.price}
              required
              className="w-full p-2 border border-gray-300 rounded"
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">دسته‌بندی‌ها</label>
            <input
              type="text"
              name="categories"
              defaultValue={product.categories.join(', ')}
              required
              className="w-full p-2 border border-gray-300 rounded"
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">موجودی</label>
            <input
              type="number"
              name="stock"
              defaultValue={product.stock}
              required
              className="w-full p-2 border border-gray-300 rounded"
            />
          </div>
          
          <div className="md:col-span-2">
            <label className="block text-sm font-medium text-gray-700 mb-1">توضیحات</label>
            <textarea
              name="description"
              defaultValue={product.description}
              required
              className="w-full p-2 border border-gray-300 rounded"
              rows="4"
            />
          </div>
          
          <div className="md:col-span-2">
            <label className="block text-sm font-medium text-gray-700 mb-1">آدرس تصویر</label>
            <input
              type="text"
              name="imageUrl"
              defaultValue={product.imageUrl}
              required
              className="w-full p-2 border border-gray-300 rounded"
            />
            <img src={product.imageUrl} alt="Preview" className="mt-2 h-32 object-contain" />
          </div>
        </div>
        
        <div className="mt-6 flex justify-end space-x-4">
          <button
            type="button"
            onClick={() => router.push('/dashboard/products')}
            className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
          >
            انصراف
          </button>
          <button
            type="submit"
            className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
          >
            ذخیره تغییرات
          </button>
        </div>
      </form>
    </div>
  );
}